.form-checkbox__title-wrapper {
  margin-bottom: 34px;

  @media (min-width: $tablet-width) {
    margin-bottom: 57px;
  }
}

.form-checkbox__list-container {
  @media (min-width: $tablet-width) and (max-width: $max-tablet-width) {
    padding: 0 70px;
  }
}

.form-checkbox__list {
  display: flex;
  flex-direction: column;
  gap: 13px;
  font-family: $osvald-font;


  @include font-size-height (16px, 18px);
  @include style-list-none;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 284px;
    grid-template-rows: auto;
    row-gap: 32px;
    column-gap: 0;

    @include font-size-height (20px, 30px);
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(4, 245px);
    column-gap: 80px;
  }
}

.form-checkbox__label {
  display: flex;
  align-items: center;

  @include transition (opacity);

  &:hover .form-checkbox__mark {
    border: 1px solid #bababa;
  }

  &:disabled {
    opacity: 0.3;
  }
}

.form-checkbox__input {
  &:focus + .form-checkbox__mark {
    border: 1px solid $basic-green;
    box-shadow: 0 0 0 1px $basic-green;
  }

  &:checked + .form-checkbox__mark {
    background-image: url("../img/tick-checkbox.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 26px 26px;

    @media (min-width: $tablet-width) {
      background-image: url("../img/tick-checkbox-tablet.svg");
    }
  }
}

.form-checkbox__mark {
  width: 28px;
  height: 28px;
  border: 1px solid #cdcdcd;

  @media (min-width: $tablet-width) {
    width: 38px;
    height: 38px;
  }
}

.form-checkbox__description {
  padding-left: 15px;

  @media (min-width: $tablet-width) {
    padding-left: 20px;
  }
}
