.main-header__toggle {
  height: 24px;
  width: 24px;
  padding: 0;
  display: flex;
  padding-top: 10px;
  border: none;
  cursor: pointer;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header__toggle--nojs {
  opacity: 0;
}

.main-header__toggle-line {
  width: 24px;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
  position: relative;
  transition-property: width, border-top, border-bottom;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}

.main-header__toggle-line::before {
  position: absolute;
  content: "";
  width: 24px;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
  top: 0;
  left: 0;
  transform: rotate(0) translateY(-9px);
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}

.main-header__toggle-line::after {
  position: absolute;
  content: "";
  width: 24px;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
  top: 0;
  left: 0;
  transform: rotate(0) translateY(7px);
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
}

.main-header__toggle-line--active {
  position: relative;
  width: 0;
  border-top: none;
  border-bottom: none;
}

.main-header__toggle-line--active::before {
  position: absolute;
  content: "";
  width: 24px;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
  top: unset;
  left: 0;
  transform: rotate(-45deg) translateY(0);
}

.main-header__toggle-line--active::after {
  position: absolute;
  content: "";
  width: 24px;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
  bottom: unset;
  left: 0;
  transform: rotate(45deg) translateY(0);
}

