.input {
  font-family: $osvald-font;

  @include font-size-height (16px, 18px);
  text-transform: uppercase;
  padding: 13px 15px 12px;
  border: 1px solid #e7e7e7;

  @include transition (border);

  &:focus {
    outline: none;
    border: 1px solid $dark-grey;
    box-shadow: 0 0 0 1px $dark-grey;

    & + .form__label-icon {
      color: $basic-green;
    }
  }

  &:invalid {
    border: 1px solid #ff8282;

    & + .form__label-icon {
      color: #ff8282;
    }
  }

  @media (min-width: $tablet-width) {
    @include font-size-height (20px, 30px);
    padding: 10px 18px;
  }

  &::placeholder {
    color: $dark-grey;
  }

  &--number::-webkit-outer-spin-button,
  &--number::-webkit-inner-spin-button {
    appearance: none;
    margin: 0;
  }

  &--email {
    padding-right: 48px;

    @media (min-width: $tablet-width) {
      padding-left: 19px;
    }

    @media (min-width: $desktop-width) {
      width: 494px;
      box-sizing: border-box;
    }
  }

  &--tel {
    padding-right: 40px;

    @media (min-width: $tablet-width) {
      padding-left: 19px;
    }

    @media (min-width: $desktop-width) {
      width: 472px;
      box-sizing: border-box;
    }
  }
}
